@import 'variables';

body.dimmed {
  &:before {
    content: '';
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 11;
    background-color: rgba(0, 0, 0, 0.7);
  }

  .modal {
    position: fixed;
    width: 444px;
    //height: 252px;
    box-sizing: border-box;
    padding: 32px 0;
    z-index: 12;
    background-color: $text-color-white;
    border-radius: 8px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    visibility: visible;
  }
}

.modal {
  position: absolute;
  visibility: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  &__title {
    text-align: center;
    margin: 16px 0 8px 0;
    font-size: $fs-title;
    font-family: $ff-medium;
    font-weight: 600;
  }

  &__info {
    text-align: center;
    margin-bottom: 24px;
  }

  &__image {
    font-size: 40px;
  }

  &__buttons-layout {
    margin-top: 16px;
  }
}



