@import 'variables';

.edit-button {
  position: absolute;
  width: 24px;
  height: 24px;
  right: 8px;
  top: 8px;
  border-radius: 50%;
  background-color: $button-color-disabled;
  cursor: pointer;
  pointer-events: auto;
  padding: 0;

  img {
    margin: 0 auto;
  }

  &__pen {
    opacity: 0.43;

    &:hover {
      opacity: 1;
    }
  }
}



