@import 'variables';

.meeting-info {
  border-radius: 8px;
  width: 340px;
  height: 112px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 13%);
  z-index: 10;
  background-color: $text-color-white;
  box-sizing: border-box;
  padding: 16px;
  //pointer-events: none;
  //filter: drop-shadow(0px 2px 5px rgba(0,0,0, .3));
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.3);

  &.hidden {
    visibility: hidden;
    display: none;
  }

  &__corner {
    width: 0;
    height: 0;
    position: absolute;
    left: 50%;
    bottom: 77%;
    transform: translate(-50%, -50%);
    border: 15px solid transparent;
    border-bottom: 18px solid $text-color-white;
  }

  &__title {
    font-family: $ff-medium;
    margin-bottom: 10px;
    margin-top: 3px;
    font-weight: bold;
  }

  &__avatar-wrapper {
    margin-top: 17px;
    display: flex;
    align-items: center;
    font-size: $fs-medium;

    .members {
      margin-left: 8px;
      span {
        color: $text-color-disabled;
      }
    }
  }

  &__time-place {
    font-size: $fs-medium;
  }
}

.avatar-round {
  width: 24px;
  height: 24px;
  border-radius: 50%;
}



