@import 'variables';

.input-block {
  margin-top: 24px;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  padding: 0 16px;
  position: relative;
  //flex-basis: 420px;

  &__label {
    font-size: $ff-medium;
    font-family: $ff-medium;
    padding-bottom: 5px;
    font-weight: 600;
  }

  &__input {
    max-width: 420px;
    width: 32.8vw;
    min-width: 328px;
    height: 38px;
    border-radius: 4px;
    border: 2px solid $button-color-disabled;
    background: #ffffff !important;
    outline: none;
    font-size: $fs-medium;
    box-sizing: border-box;
    padding-left: 10px;
  }

  &__input:focus {
    color: #000000;
    border: 2px solid $button-color-normal;
  }

  &__icon_calendar {
    position: absolute;
    right: 30px;
    bottom: 13px;
    opacity: 0.5;
  }

  &__input_data {
    min-width: 238px;
    max-width: 238px;
    width: 18vw;
  }

  &__input_time {
    width: 72px;
    min-width: 72px;
  }
}

