.input-block__input_data::-ms-clear {
  display: none;
}

.input-block__input_data::-webkit-datetime-edit,
.input-block__input_data::-webkit-inner-spin-button,
.input-block__input_data::-webkit-clear-button,
.input-block__input_time::-webkit-inner-spin-button,
.input-block__input_time::-webkit-clear-button {
  display: none;
}

.input-block__input_data::-webkit-calendar-picker-indicator,
.input-block__input_data::-webkit-calendar-picker-indicator:hover {
  position: absolute;
  right: 30px;
  color: transparent;
  background-color: transparent;
  opacity: 1;
  cursor: pointer;
}



