@import 'variables';

.add-meeting-ui {
  position: relative;
  top: $header-height;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  height: calc(100vh - 70px);

  &__main {
    height: 100vh;
    width: 100vw;
    //height: 100%;
    overflow-y: scroll;
    //display: flex;
    //flex-direction: column;
    //justify-content: space-between;
  }

  &__layout {
    //max-width: 875px;
    display: flex;
    flex-direction: column;
    position: relative;
    margin: 0 auto;
    width: fit-content;
    top: 33px;
  }

  &__title {
    margin-left: 16px;
    font-size: $fs-title;
    font-weight: 600;
  }

  &__footer {
    box-sizing: border-box;
    padding: 20px;
    width: 100vw;
    display: flex;
    justify-content: center;
    border-top: $separator-block-setting;

    //&.footer_edit {
    //	position: absolute;
    //	top: calc(100vh - 119px);
    //}
  }
}

.add-meeting-ui__main {
  & .button-remove {
    visibility: hidden;
    width: 0;
    height: 0;
  }
}



